import React, { useState, useEffect } from "react"
import Aria2Client from "./aria2-client"


export function useWindowSize() {
  const [size, set] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  })

  useEffect(() => {
    function onResize(e: any) {
      set({
        width: window.innerWidth,
        height: window.innerHeight
      })
    }

    window.addEventListener('resize', onResize)

    return () => window.removeEventListener('resize', onResize)
  }, [])

  return {width: size.width, height: size.height}
}

export function useInput(init = '') {
  var [value, setValue] = useState(init)

  return {
    value   : value,
    setValue: (v: string) => setValue(v),
    clear   : () => setValue(''),
    onChange: (e: React.ChangeEvent<HTMLInputElement>) => setValue(e.target.value)
  }
}

export function useActive(aria2: Aria2Client, interval: number) {
  const [list, set] = useState<any>([])

  useEffect(() => {
    set([])
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.tellActive().then((list: any) => {
          set(list)
        }, () => {set([])})
      }
    }, interval)

    return () => clearInterval(t)
  }, [aria2, interval])



  return list
}

export function useWaiting(aria2: Aria2Client, interval: number) {
  const [list, set] = useState<any>([])

  useEffect(() => {
    set([])
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.tellWaiting(0, 100).then((list: any) => {
          set(list)
        }, () => {set([])})
      }
    }, interval)
    return () => clearInterval(t)
  }, [aria2, interval])

  return list
}

export function useComplete(aria2: Aria2Client, interval: number) {
  const [list, set] = useState<any>([])

  useEffect(() => {
    set([])
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2) {
        aria2.tellStopped(0, 100).then((list: any) => {
          set(list)
        }, () => {set([])})
      }
    }, interval)
    return () => clearInterval(t)
  }, [aria2, interval])

  return list
}

export function useStatus(aria2: Aria2Client, gid: string) {
  const [info, set] = useState({})

  useEffect(() => {
    set({})
  }, [aria2])

  useEffect(() => {
    const t = setInterval(() => {
      if (aria2 && gid) {
        aria2.tellStatus(gid).then((info: any) => {
          set(info)
        }, () => {set({})})
      } else set({})
    }, 1000)

    return () => clearInterval(t)
  }, [aria2, gid])

  return info
}

export function useGlobalOption(aria2: Aria2Client) {
  const [option, set] = useState({})

  useEffect(() => {
    set({})
  }, [aria2])

  useEffect(() => {
    if (aria2) {
      aria2.getGlobalOption().then((option: any) => {
        set(option)
      }, () => {set({})})
    }
  }, [aria2])

  return { option, updateOption: () => {set({...option})}}
}

