import './App.css';

import Aria2Client from './aria2-client';
import { useEffect, useMemo, useState } from 'react';
import { ServerInfo } from './interfaces'
import { useWindowSize } from './hooks'
import {
  ActiveList, CompletedList,
  Setting, SelectUser
} from './components'

// 不要改顺序
const tablist = ['任务队列', '已完成', '设置']


function App() {

  const { width } = useWindowSize()
  const viewType  = width > 999
    ? 'container box'
    : 'container fullscreen';

  const [activeIdx, setActiveIdx] = useState(0)

  const __localStorage_servers: ServerInfo[] = JSON.parse(
    localStorage.getItem('servers') || '[]'
  )
  const [
    localServers, setLocalServers
  ] = useState(__localStorage_servers )
  useEffect(() => {
    localStorage.setItem(
      'servers',
      JSON.stringify(localServers)
    )
  }, [localServers])

  const [
    selectServerName, setSelectServerName
  ] = useState(
    localServers.length
    ? localServers[0].name
    : ''
  )
  useEffect(() => {
    const selectServerInfo = localServers.find(
      it => it.name === selectServerName
    )
    if (
      selectServerInfo &&
      selectServerInfo.ip &&
      selectServerInfo.port
    ) {
      const aria2 = new Aria2Client(
        selectServerInfo.ip,
        selectServerInfo.port,
        selectServerInfo.secret
      )
      setAria2Client(aria2)
    } else {
      setAria2Client(null)
    }
  }, [selectServerName, localServers])

  const [aria2Client, setAria2Client] = useState(
    useMemo(() => {
      const selectServerInfo = localServers.find(
        it => it.name === selectServerName
      )
      if (
        selectServerInfo &&
        selectServerInfo.ip &&
        selectServerInfo.port
      ) {
        const aria2 = new Aria2Client(
          selectServerInfo.ip,
          selectServerInfo.port,
          selectServerInfo.secret
        )
        return aria2
      } else {
        return null
      }
    }, [localServers, selectServerName])
  )

  return (
    <div className={viewType}>


      <div className='sidebar'>

        <div className="userInfo">
          <SelectUser servers={localServers}
            select={{selectServerName, setSelectServerName}} />
        </div>

        <ul className='tabs'>{
          tablist.map((tab, idx) => {
            return (
              <li key={idx} onClick={() => setActiveIdx(idx)}
                className={activeIdx === idx ? 'active' : ''}>
                {tab}
              </li>
            )
          })
        }</ul>

      </div>

      <div className='content'>

        <ActiveList aria2Client={aria2Client} active={activeIdx === 0} />
        <CompletedList aria2Client={aria2Client} active={activeIdx === 1} />
        <Setting aria2Client={aria2Client} active={activeIdx === 2}
          useStateServers={[localServers, setLocalServers]}/>

      </div>

    </div>
  );
}

export default App;
