import { useRef, useState } from "react";
import { useActive, useComplete, useGlobalOption, useWaiting, useInput } from './hooks'
import _fileSize from './_filesize'

const fileStatus = {
  'complete': '已完成',
  'active'  : '下载中',
  'paused'  : '已暂停'
}

export function ActiveList(props) {
  const {aria2Client, active}       = props
  const activeList                  = useActive(aria2Client, 1000)
  const stoppedList                 = useWaiting(aria2Client, 1000)
  const list                        = [...activeList, ...stoppedList]
  const styleDisplay                = active ? '' : 'none'
  const [clickedGid, setClickedGid] = useState('')

  const textareaHook                    = useInput()
  const configSpeedHook                 = useInput()
  const [showOption, setShowOption]     = useState(false)
  const [showAddTasks, setShowAddTasks] = useState(false)

  function addTasks() {
    let tasks = textareaHook.value.split('\n')
      .map(it => it.trim()).filter(it => it.length)
    console.log('add tasks', tasks)
    let configs = {
      'max-download-limit': configSpeedHook.value
    }
    console.log('configs', configs)
    tasks.forEach(task => {
      if (configs['max-download-limit'].length) {
        aria2Client.addUri([task], configs)
      } else aria2Client.addUri([task])
    })
    textareaHook.clear()
    configSpeedHook.clear()
    setShowAddTasks(false)
  }
  function cancelAddTasks() {
    textareaHook.clear()
    configSpeedHook.clear()
    setShowAddTasks(false)
  }
  function changeToOption() {
    setShowAddTasks(false)
    setShowOption(true)
  }
  function cancelOption() {
    setShowOption(false)
    setShowAddTasks(true)
    configSpeedHook.clear()
  }
  function addOption() {
    setShowOption(false)
    setShowAddTasks(true)
  }
  function changeShowInfo(gid) {
    if (gid === clickedGid) setClickedGid('')
    else setClickedGid(gid)
  }
  function hiddenListInfo(e) {
    if (e.target.tagName === 'UL') setClickedGid('')
  }

  return (
    <div className="content-container" style={{display: styleDisplay}}>
      <div className='header'>
        { list.length
          ? <>
              <button onClick={() => aria2Client.unpauseAll()}>全部开始</button>
              <button onClick={() => aria2Client.pauseAll()}>全部暂停</button>
            </>
          : ''
        }
        { aria2Client && aria2Client.ws.readyState === 1
          ? <button onClick={() => {setShowAddTasks(true)}}>新建下载</button>
          : ''
        }
      </div>
      <div className="body">
        { list.length // 任务列表
          ? <ul className="content-list" onClick={hiddenListInfo}> {
              list.filter(it => it.status !== "error").map(it => {
                const className    = `content-item ${clickedGid === it.gid ? " clicked" : ''}`
                const percentage   = (it.completedLength / it.totalLength * 100).toFixed(2)
                const styleBgimage = 'linear-gradient(to right, ' +
                  `rgba(0, 132, 255, 0.5) ${percentage || 0}%,` +
                  'rgb(255, 255, 255, 0) 10%, rgb(255, 255, 255, 0))'

                return (
                  <li key={it.gid} className={className} style={{backgroundImage: styleBgimage}}
                    onClick={() => changeShowInfo(it.gid)}>
                    <p>{it.files[0].path.split('/').at(-1)}</p>
                    <p>{percentage ? percentage + '%' : ''}</p>
                    <p>{_fileSize(it.downloadSpeed)}/s</p>
                  </li>
                )
              })
            }</ul>
          : aria2Client && aria2Client.ws.readyState === 1
            ? <h2>无任务</h2>
            : <h2>请检查服务器配置</h2>
        }

        { showAddTasks && // 添加下载任务界面
          <div id="add-tasks">
            <textarea autoFocus
              placeholder='添加多个任务时请确保每行仅一个链接'
              onChange={textareaHook.onChange} value={textareaHook.value}
            />
            <div className='btns'>
              <button onClick={() => {changeToOption()}}>配置</button>
              <button onClick={() => {cancelAddTasks()}}>取消</button>
              <button onClick={() => {addTasks()}}>立即下载</button>
            </div>
          </div>
        }

        { showOption && // 设置下载配置项界面
          <div id="config">
            <div className='form'>
              <div className='form-item'>
                <span>下载速度：</span>
                <input type="text" value={configSpeedHook.value}
                  placeholder='不填即为不限速；例：1k'
                  onChange={configSpeedHook.onChange}/>
                <span> /s </span>
              </div>
            </div>
            <div className='btns'>
              <button onClick={() => {cancelOption()}}>取消</button>
              <button onClick={() => {addOption()}}>确认</button>
            </div>
          </div>
        }

        { list.length ? // 任务详细信息
          <ListInfo info={list.find(it => it.gid && it.gid === clickedGid)} aria2Client={aria2Client}/>
          : ''
        }
      </div>
    </div>
  )
}

export function CompletedList(props) {
  const {aria2Client, active}       = props
  const list                        = useComplete(aria2Client, 2000)
  const styleDisplay                = active ? '' : 'none'
  const [clickedGid, setClickedGid] = useState('')

  function changeShowInfo(gid) {
    setClickedGid(gid === clickedGid ? '' : gid)
  }
  function hiddenListInfo(e) {
    if (e.target.tagName === 'UL') setClickedGid('')
  }

  return (
    <div className="content-container" style={{display: styleDisplay}}>
      <div className="header">
        { list.length
          ? <button onClick={() => aria2Client.purgeDownloadResult()}>清空列表</button>
          : ''
        }
      </div>

      <div className="body">
        { list.length
          ? <ul className="content-list" onClick={hiddenListInfo}>{
              list.filter(it => it.status === "complete").map(it => {
                const className = `content-item ${clickedGid === it.gid ? " clicked" : ''}`
                return (
                  <li key={it.gid} className={className}
                    onClick={() => changeShowInfo(it.gid)}>
                    <p>{it.files[0].path.split('/').at(-1)}</p>
                    <p>{_fileSize(it.files[0].length)}</p>
                  </li>
                )
              })
            }</ul>
          : aria2Client && aria2Client.ws.readyState === 1
            ? <h2>暂无已完成任务</h2>
            : <h2>请检查服务器配置</h2>
        }
        { list.length
          ? <ListInfo info={
              list.find(it => it.gid && it.gid === clickedGid)
            } aria2Client={aria2Client}/>
          : ''
        }

      </div>
    </div>
  )
}

export function ListInfo(props) {
  const { aria2Client, info } = props

  if (info && Object.keys(info).length) {
    const percentage = (
      info.completedLength / info.totalLength * 100
    ).toFixed(2)
    return (
      <table className="list-info">
        <tbody>
          <tr>
            <td>进度：{percentage ? percentage + '%' : ''}</td>
            <td>已下载：{_fileSize(info.completedLength)}</td>
            <td>文件大小：{_fileSize(info.totalLength)}</td>
          </tr>

          <tr>
            <td>状态：{fileStatus[info.status] || info.status}</td>
            <td>分片数：{info.numPieces}</td>
            <td>分片大小：{_fileSize(info.pieceLength)}</td>
          </tr>

          <tr>
            <td>保存路径：{info.dir}</td>
            <td>连接数：{info.connections}</td>
              <td rowSpan={2}>
                { info.status === 'active'
                  ? <span className="btn"
                    onClick={() => {aria2Client.remove(info.gid)}}>删除任务</span>
                  : <span className="btn"
                    onClick={() => aria2Client.removeDownloadResult(info.gid)}>删除任务</span>
                }
                { info.status === 'active' &&
                  <span className="btn"
                    onClick={() => {aria2Client.pause(info.gid)}}>暂停下载</span>
                }
                { info.status === 'paused' &&
                  <span className="btn"
                    onClick={() => {aria2Client.unpause(info.gid)}}>继续下载</span>
                }
              </td>
          </tr>
        </tbody>
      </table>

    )
  }
  return ''
}

export function Setting(props) {
  const {aria2Client, active, useStateServers} = props
  const [
    localServers, setLocalServers
  ] = useStateServers

  const newServerOptionRefs = { // 不要改顺序
    name  : useRef(''),
    ip    : useRef(''),
    port  : useRef(''),
    secret: useRef(''),
  }

  const styleDisplay                      = active ? '' : 'none'
  const [showAddServer, setShowAddServer] = useState(false)
  const {option, updateOption}            = useGlobalOption(aria2Client)
  const [changed, setChanged]             = useState(false)

  const optionText = {
    'dir'               : '文件保存路径：',
    'max-download-limit': '最大下载速度（0表示不限速，例如：1k, 1M）：'
  }
  const optionRefs = {
    'dir'               : useRef(optionText['dir']),
    'max-download-limit': useRef(optionText['max-download-limit']),
  }

  function cancelOptionChange() {
    setChanged(false)
    Object.keys(optionRefs).forEach(key => {
      optionRefs[key].current.value = option[key]
    })
  }
  async function applyOptionChange() {
    const diff = {}
    Object.keys(optionRefs).forEach(key => {
      if (optionRefs[key].current.value !== option[key]) {
        diff[key] = optionRefs[key].current.value
      }
    })
    await aria2Client.changeGlobalOption(diff)
    setChanged(false)
    updateOption()
  }

  function addServer() {
    const newServerOption = {}
    Object.keys(newServerOptionRefs).forEach(key => {
      newServerOption[key] = newServerOptionRefs[key].current.value
    })

    setLocalServers(() => {
      setLocalServers(() => [...localServers, newServerOption])
    })
    setShowAddServer(false)
  }

  function deleteServer(name) {
    setLocalServers(() => {
      return [...localServers.filter(it => it.name !== name)]
    })
  }

  return (
    <div className="content-container" style={{display: styleDisplay}}>
      <div className="header">
        { changed &&
          <button onClick={cancelOptionChange}>取消更改</button> }
        { changed &&
          <button onClick={applyOptionChange}>应用更改</button> }
      </div>

      <div className="body">
        { Object.keys(option).length // 全局配置项
          ? <ul className="content-list">{
              Object.keys(optionRefs).map(key => {
                return (
                  <li className='content-item' key={key}>
                    <span>{optionText[key]}</span>
                    <input type="text" ref={optionRefs[key]}
                      defaultValue={option[key]}
                      onChange={() => setChanged(true)}/>
                  </li>
                )
              })
            }</ul>
          : <h2>未连接</h2>
        }

        <div className="server-list">
          <div className="server-list-title">
            <h3>服务器列表</h3>
            <button onClick={() => setShowAddServer(true)}>
              添加新服务器
            </button>
          </div>
          <div className="server-list-content">
            <ul>{
              localServers.map((it, idx) => {
                return (
                  <li key={idx}>
                    <p>{it.name}</p>
                    <p>{it.ip}:{it.port}</p>
                    <p>{it.secret}</p>
                    <button onClick={() => {deleteServer(it.name)}}>删除</button>
                  </li>
                )
              })
            }</ul>
          </div>

        </div>

        { showAddServer && // 新服务器添加界面
          <div id="add-server">
            <table>
              <tbody>
                <tr>
                  <td>用户名</td>
                  <td><input type="text" ref={newServerOptionRefs.name}/></td>
                </tr>
                <tr>
                  <td>IP</td>
                  <td><input type="text" ref={newServerOptionRefs.ip}/></td>
                </tr>
                <tr>
                  <td>端口号</td>
                  <td><input type="text" ref={newServerOptionRefs.port}/></td>
                </tr>
                <tr>
                  <td>secret</td>
                  <td><input type="text" ref={newServerOptionRefs.secret}/></td>
                </tr>
              </tbody>
            </table>
            <div className="btns">
              <button onClick={() => setShowAddServer(false)}>取消</button>
              <button onClick={addServer}>确认</button>
            </div>
          </div>
        }
      </div>
    </div>
  )
}

export function SelectUser(props) {
  const { servers, select } = props

  function handleChange(e) {
    select.setSelectServerName(e.target.value)
  }

  return (
    <select value={select.selectServerName} onChange={handleChange}>
      { servers.length
        ? servers.map((it, idx) => {
            return (
              <option key={idx} value={it.name}>{it.name}</option>
            )
          })
        : <option value={undefined}>默认</option>
      }
    </select>
  );
}
